.box {
  display: flex;
  align-items: center;
  background-color: rgb(125 201 236 / 32%);
  margin-top: 10%;
  padding:50px;
}

.image-container {
  flex: 1;
 
}

.image-container img{
  max-width: 600px; /* Set the maximum width of the image */
  height: auto; /* Maintain the aspect ratio */
}



.text-container {
  flex: 1;
  padding-left: 20px; 
  font-size: x-large;
}

.impact{
  text-align: center;
  
  padding-top: 5%;
  padding-bottom: 5%;
}
#fimage{
 margin-left:20%;
}
.image-container-abt {
  position: relative;
  width: 100%; 
  margin-top: 60px;
}
.image-container-abt img {
  width: 100%;
  height: 600px;
 
}
.image-container-abt::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 80%;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
  
}

.text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  color: #fff;
  text-align: center;
  font-size: 60px;

}

